<template>
  <div>
    <dt-modal
      :title="title"
      :banner-title="bannerTitle"
      :close-button-props="buttonCloseProps"
      :show="isOpen"
      :kind="kind"
      :size="size"
      :copy="copy"
      :modal-class="modalClass"
      :banner-class="bannerClass"
      :dialog-class="dialogClass"
      :hide-close="hideClose"
      :labelled-by-id="labelledById"
      :fixed-header-footer="fixedHeaderFooter"
      @update:show="close"
    >
      <template
        v-if="banner"
        slot="banner"
      >
        <span v-html="banner" />
      </template>
      <template
        v-if="header"
        slot="header"
      >
        <span v-html="header" />
      </template>
      <template
        v-if="defaultSlot"
        slot="default"
      >
        <span v-html="defaultSlot" />
      </template>
      <template
        v-if="showFooter"
        slot="footer"
      >
        <span
          v-if="footer"
          v-html="footer"
        />
        <div v-else>
          <dt-button
            :kind="kind"
            importance="primary"
          >
            Confirm
          </dt-button>
          <dt-button
            :kind="kind"
            importance="clear"
          >
            Cancel
          </dt-button>
        </div>
      </template>
    </dt-modal>
    <dt-button
      @click="isOpen = !isOpen"
    >
      Click to open
    </dt-button>
  </div>
</template>

<script>
import DtModal from './modal';
import { DtButton } from '../button';

export default {
  name: 'DtModalDefault',

  components: {
    DtModal,
    DtButton,
  },

  props: {
    showFooter: {
      type: Boolean,
      default: false,
    },
  },

  data () {
    return {
      isOpen: this.show,
    };
  },

  computed: {
    buttonCloseProps () {
      return {
        ...this.closeButtonProps,
        ariaLabel: 'Close',
      };
    },
  },

  watch: {
    show: {
      handler () {
        this.isOpen = this.show;
      },
    },
  },

  methods: {
    close (event) {
      this.isOpen = !this.isOpen;
      this.onClose(event);
    },
  },

};
</script>
